import React, { useState, useRef, useEffect } from 'react';
import { Slider } from 'antd';
import { WidgetProps, WidgetPath } from '../index';
import { WidgetWrap } from '../index.styled';
import styled from 'styled-components';

// const marks: SliderMarks = {
//   320: '-',
//   1440: '+',
// };

const StyledSliderWrap = styled.div`
  width: 100%;

  .ant-slider {
    margin-left: 0;
    margin-right: 0;
  }
  .ant-slider-rail {
    background-color: #e9e9e9;
  }
`;

const StyledSliderBtns = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-top: -5px;

  > button {
    border: 0;
    outline: 0;
    padding: 0;
    cursor: pointer;
    background-color: transparent;

    &[name='-'] {
      padding-right: 10px;
    }
    &[name='+'] {
      padding-left: 10px;
    }
  }
`;

export const DEFAULT_STEP = 5;
export const DEFAULT_MAX = 1440;
export const DEFAULT_MIN = 320;
export const DEFAULT_CONTENT_AREA_WIDTH = 600;

function WidgetContentAreaWidth({ value: valueProp, initValue: initValueProp, onChange }: WidgetProps<string>) {
  const debounceRef = useRef<NodeJS.Timeout>();
  const defaultValue = typeof initValueProp === 'undefined' ? DEFAULT_CONTENT_AREA_WIDTH : parseInt(initValueProp);
  const [value, setValue] = useState(() => {
    return !valueProp ? defaultValue : parseInt(valueProp);
  });

  useEffect(() => {
    if (valueProp) {
      setValue(parseInt(valueProp));
    }
  }, [valueProp]);

  function handleChange(newVal: number) {
    setValue(newVal);

    if (debounceRef.current) {
      clearTimeout(debounceRef.current);
      debounceRef.current = undefined;
    }

    debounceRef.current = setTimeout(() => {
      onChange(WidgetPath.ContentAreaWidth, `${newVal}px`);
    }, 300);
  }

  function handleClick(evt: any) {
    const name = evt.target.name;
    if (name === '+' && value + DEFAULT_STEP <= DEFAULT_MAX) {
      handleChange(value + DEFAULT_STEP);
    }
    if (name === '-' && value - DEFAULT_STEP >= DEFAULT_MIN) {
      handleChange(value - DEFAULT_STEP);
    }
  }

  return (
    <WidgetWrap>
      <label>Content area width</label>
      <label>{value}px</label>
      <StyledSliderWrap>
        <Slider
          // marks={marks}
          value={value}
          defaultValue={defaultValue}
          min={DEFAULT_MIN}
          max={DEFAULT_MAX}
          step={DEFAULT_STEP}
          onChange={handleChange}
        />
        <StyledSliderBtns>
          <button onClick={handleClick} name="-">
            -
          </button>
          <button onClick={handleClick} name="+">
            +
          </button>
        </StyledSliderBtns>
      </StyledSliderWrap>
    </WidgetWrap>
  );
}

export { WidgetContentAreaWidth };
